@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&subset=cyrillic);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  color: #05386b;
  background-color: #5cdb95;
}

.taskForm  {
  width: 50%;
  display: flex;
  margin: auto;
  flex-direction: column;
  justify-content: center;
}

.todo-list {
  padding: 0;
}

.todo-item__container {
  display: inline-block;
  width: 100%;
  background: #edf5e1;
  border: 5px solid #05386b;
  padding: 0.5rem;
}

.todo-item {
  display: inline-block;
  cursor: pointer;
  font-size: 2rem;
  font-weight: bolder;
  margin: 0 auto;
  line-height: 1;
}

.completed {
  text-decoration: line-through;
}

.ui.button,
.ui.button:active,
.ui.button:hover {
  background-color: #edf5e1;
  color: #05386b;
}


.main-header { 
  box-shadow: 0 2px 1px 1px rgba(5, 56, 107, 0.5);
  margin: 1rem auto;
}

.main-header__title {
  display: inline-block;
}


.App {
  text-align: center;
}
